'use client';

import Image from 'next/image';
import { DotIcon } from 'lucide-react';
import { useSelector } from 'react-redux';
import { useState } from 'react';
import Rating from './Rating';
import RatingModal from './RatingModal';

const OrderItem = ({ order }) => {
  const currency = process.env.NEXT_PUBLIC_CURRENCY_SYMBOL || '$';
  const [ratingModal, setRatingModal] = useState(null);

  const ratings = useSelector(state => state.rating?.ratings || []);

  /* ---------------- SAFETY CHECK ---------------- */
  if (!order || !Array.isArray(order.orderItems)) {
    return null;
  }

  /* ---------------- HELPERS ---------------- */
  const getProductImage = (item) =>
    item?.product?.images?.[0] || '/placeholder.png';

  const getProductName = (item) =>
    item?.product?.name || 'Product';

  const getOrderDate = () => {
    try {
      return new Date(order.createdAt).toDateString();
    } catch {
      return '';
    }
  };

  const hasRating = (productId) =>
    ratings.find(
      r =>
        r.orderId === order._id &&
        r.productId === productId
    );

  return (
    <>
      {/* ---------------- DESKTOP ---------------- */}
      <tr className="text-sm">
        <td className="text-left">
          <div className="flex flex-col gap-6">
            {order.orderItems.map((item, index) => (
              <div key={index} className="flex items-center gap-4">
                <div className="w-20 aspect-square bg-slate-100 flex items-center justify-center rounded-md">
                  <Image
                    src={getProductImage(item)}
                    alt="product"
                    width={60}
                    height={60}
                    className="object-contain"
                  />
                </div>

                <div className="flex flex-col text-sm">
                  <p className="font-medium text-slate-600 text-base">
                    {getProductName(item)}
                  </p>

                  <p>
                    {currency}
                    {item.price} &nbsp; Qty : {item.quantity}
                  </p>

                  <p className="mb-1">{getOrderDate()}</p>

                  {/* ---------- RATING ---------- */}
                  {hasRating(item.product?._id) ? (
                    <Rating
                      value={hasRating(item.product?._id).rating}
                    />
                  ) : (
                    <button
                      onClick={() =>
                        setRatingModal({
                          orderId: order._id,
                          productId: item.product?._id,
                        })
                      }
                      className={`text-green-500 hover:bg-green-50 transition ${
                        order.status !== 'DELIVERED' && 'hidden'
                      }`}
                    >
                      Rate Product
                    </button>
                  )}
                </div>
              </div>
            ))}
          </div>
        </td>

        <td className="text-center max-md:hidden">
          {currency}
          {order.total}
        </td>

        <td className="text-left max-md:hidden">
          <p>
            {order.address?.name},{' '}
            {order.address?.street}
          </p>
          <p>
            {order.address?.city}, {order.address?.state},{' '}
            {order.address?.zip}, {order.address?.country}
          </p>
          <p>{order.address?.phone}</p>
        </td>

        <td className="text-left max-md:hidden">
          <div
            className={`flex items-center justify-center gap-1 rounded-full p-1
              ${
                order.status === 'CONFIRMED'
                  ? 'text-yellow-500 bg-yellow-100'
                  : order.status === 'DELIVERED'
                  ? 'text-green-500 bg-green-100'
                  : 'text-slate-500 bg-slate-100'
              }
            `}
          >
            <DotIcon size={10} className="scale-150" />
            {order.status?.toLowerCase()}
          </div>
        </td>
      </tr>

      {/* ---------------- MOBILE ---------------- */}
      <tr className="md:hidden">
        <td colSpan={5}>
          <p>
            {order.address?.name},{' '}
            {order.address?.street}
          </p>
          <p>
            {order.address?.city}, {order.address?.state},{' '}
            {order.address?.zip}, {order.address?.country}
          </p>
          <p>{order.address?.phone}</p>

          <div className="flex justify-center mt-2">
            <span className="px-6 py-1.5 rounded bg-green-100 text-green-700">
              {order.status?.replace(/_/g, ' ').toLowerCase()}
            </span>
          </div>
        </td>
      </tr>

      <tr>
        <td colSpan={4}>
          <div className="border-b border-slate-300 w-6/7 mx-auto" />
        </td>
      </tr>

      {/* ---------- RATING MODAL ---------- */}
      {ratingModal && (
        <RatingModal
          ratingModal={ratingModal}
          setRatingModal={setRatingModal}
        />
      )}
    </>
  );
};

export default OrderItem;
